.loan-form-container {
  max-width: 750px;
  margin: 0 auto;
  background: #DCE35B;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to bottom, #45B649, #DCE35B);
  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, left top, left bottom, from(#45B649), to(#DCE35B));
  background: -webkit-linear-gradient(top, #45B649, #DCE35B);
  background: linear-gradient(to bottom, #45B649, #DCE35B);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */ }
  .loan-form-container .input-container {
    margin-bottom: 10px; }
    .loan-form-container .input-container.submit-button {
      height: auto;
      margin-top: 15px; }
  .loan-form-container .form-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: stretch; }
    .loan-form-container .form-container.interest input[type=number], .loan-form-container .form-container.duration input[type=number] {
      padding-left: 40px;
      margin-right: -40px; }
    .loan-form-container .form-container.duration input[type=number] {
      margin-right: 0px; }
    .loan-form-container .form-container .symbol {
      background: #FFF;
      width: 40px;
      height: 40px;
      line-height: 40px;
      text-align: center;
      z-index: 3; }
  .loan-form-container .error-msg-container {
    text-align: center;
    background-color: #FFF;
    margin-top: 20px;
    border: 3px solid red;
    color: red;
    padding: 20px 20px 25px 20px;
    border-radius: 5px;
    font-weight: bold;
    text-transform: uppercase;
    font-size: 14px;
    line-height: 18px; }
  .loan-form-container label,
  .loan-form-container input[type=number] {
    display: block;
    font-size: 16px;
    position: relative; }
  .loan-form-container label {
    margin-bottom: 5px; }
  .loan-form-container input[type=number] {
    flex-grow: 1;
    margin: 0;
    padding: 0;
    height: 40px;
    background: #FFF;
    border: none;
    color: #696969;
    outline: none; }
  .loan-form-container input[type=submit] {
    border: none;
    display: block;
    background: #6495ED;
    height: 50px;
    width: 100%;
    color: #FFF;
    text-align: center;
    border-radius: 3px;
    font-size: 20px;
    text-transform: uppercase;
    box-shadow: 8px 8px 16px 0px #000;
    -webkit-transition: all 0.15s ease;
    transition: all 0.15s ease;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none; }
    .loan-form-container input[type=submit]:hover {
      background: #00BFFF;
      box-shadow: 2px 4px 8px 0px #000;
      outline: 0; }
    .loan-form-container input[type=submit]:focus {
      outline: 0; }
  @media only screen and (min-width: 550px) {
    .loan-form-container .input-container {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-between;
      height: 42px;
      margin-bottom: 15px; }
      .loan-form-container .input-container.submit-button {
        margin-top: 25px; }
    .loan-form-container .error-msg-container {
      width: 100%; }
    .loan-form-container .form-container {
      width: 60%; }
    .loan-form-container label,
    .loan-form-container input[type=number] {
      font-size: 20px; }
    .loan-form-container label {
      line-height: 42px;
      margin-bottom: 0px;
      width: calc(40% - 20px);
      margin-right: 20px;
      text-align: right; } }

